    import mInput from '../../components/m-input.vue'

    export default {
        components: {
            mInput
        },
        data() {
            return {
                account: '',
                password: '',
				remember:"1",
				checkArr:[],
				isCheck:false,//是否记住帐户
            }
        },

        methods: {

			checkboxChange(e){
				this.checkArr=e.detail.value;
			},
            bindLogin() {
                /**
                 * 客户端对账号信息进行一些必要的校验。
                 * 实际开发中，根据业务需要进行处理，这里仅做示例。
                 */
                if (!this.account) {
                    uni.showToast({
                        icon: 'none',
                        title: '请填写帐号'
                    });
                    return;
                }
                if (!this.password.length) {
                    uni.showToast({
                        icon: 'none',
                        title: '请填写密码'
                    });
                    return;
                }
                /**
                 * 下面简单模拟下服务端的处理
                 * 检测用户账号密码是否在已注册的用户列表中
                 * 实际开发中，使用 uni.request 将账号信息发送至服务端，客户端在回调函数中获取结果信息。
                 */
                const data = {
                    accountName: this.account,
                    password: this.password
                };
				let that=this;
				if(this.checkArr.length>0){
					uni.setStorage({
						key:"accountName",
						data:that.account
					})
				}else{
					
				}
			
				//登录
				this.$ajax({
					url:"/system_manager/login",
					method:"POST",
					data:data,
					success:function(d){
						uni.setStorage({
							key:"shopObj",
							data:JSON.stringify(d)
						})
						uni.setStorageSync('phone', d.phone||"")
						uni.reLaunch({
						    url: '../main/main',
						})
						
					}
				})
            }
        },
        onReady() {
			 
        
        },
		onLoad(){
			let that=this;
			uni.getStorage({
				key:"accountName",
				success(res){
					that.account=res.data||"";
					console.log(this,res)
				}
			})
		}
    }
